/**
 *   (Main Application Entry Point)
 *
 * 
 * 1.   Bootstrap 
 * 2.   Vue 3 
 * 3.   Vue Router 
 * 4.   Pinia 
 * 5.   DOM 
 * 6.  LeafletFont AwesomeBootstrap
 *
 * 
 * - Vue 3 (Composition API)
 * - Vue Router 4
 * - Pinia ()
 * - Bootstrap 5 (UI )
 * - Leaflet ()
 * - Font Awesome ()
 */

//  Vue  (Vue Core Module Imports)
import { createApp } from 'vue';
import { createPinia } from 'pinia';

//   (Application Component Imports)
import App from './App.vue';
import router from './router';

//   (Import Third-Party Style Files)
import 'bootstrap/dist/css/bootstrap.min.css'; // Bootstrap 5 CSS 
import 'leaflet/dist/leaflet.css'; // Leaflet 
import '@fortawesome/fontawesome-free/css/all.min.css'; // Font Awesome 

//   (Import Custom Style Files)
import './assets/css/common.css'; // 

//   JavaScript  (Import Third-Party JavaScript Files)
import 'bootstrap/dist/js/bootstrap.bundle.min.js'; // Bootstrap JS Popper.js
// 

//   (Debug Information Output)
console.log(' ');

//   Vue  (Create and Configure Vue Application Instance)
const app = createApp(App);

//   Pinia  (Create Pinia State Management Instance)
const pinia = createPinia();

//   Vue Router  (Register Vue Router Navigation System)
app.use(router);

//   Pinia  (Register Pinia State Management System)
app.use(pinia);

//   DOM  (Mount Application to DOM Element)
//  Vue  index.html  id="app" 
app.mount('#app');

//   (Application Startup Debug Information)
console.log(' ');
console.log(' Pinia ');
console.log(' Vue Router ');
console.log(' Bootstrap 5 UI ');
console.log(' Leaflet ');
console.log(' Font Awesome ');
