<script>
  import PropertiesTab from '../tabs/PropertiesTab.vue';
  import { useDataStore } from '../stores/dataStore';
  import { computed, watch } from 'vue';

  export default {
    name: 'RightView',
    components: {
      PropertiesTab, // 
    },
    props: {
      /**   */
      activeRightTab: {
        type: String,
        default: 'results',
      },
      /**   */
      analysisList: {
        type: Array,
        default: () => [],
      },
      /**   ID */
      selectedAnalysisId: {
        type: [Number, String],
        default: null,
      },
      /**   () */
      rightViewWidth: {
        type: Number,
        default: 250,
      },
    },

    /**
     *   (Component Events)
     * 
     */
    emits: [
      'update:activeRightTab', // 
      'select-analysis', // 
      'view-analysis', // 
      'delete-analysis', // 
      'highlight-feature', // 
    ],

    /**
     *   (Component Setup)
     *  Composition API 
     */
    setup(props) {
      //   Pinia 
      const dataStore = useDataStore();

      /**
       *   (Calculate Active Analysis Count)
       * 
       *
       * @returns {number} 
       */
      const getActiveAnalysisCount = () => {
        return props.analysisList.filter((a) => a.status === '').length;
      };

      /**
       *   (Watch Selected Feature Changes)
       *  Pinia store  selectedFeature 
       */
      watch(
        () => dataStore.selectedFeature,
        (newFeature) => {
          console.log('RightView - selectedFeature changed:', {
            newFeature: newFeature,
            properties: newFeature?.properties,
            store: dataStore,
          });
        },
        { immediate: true }
      ); // immediate: true 

      /**
       *   (Selected Feature Computed Property)
       *  Pinia store 
       * 
       */
      const selectedFeatureComputed = computed(() => {
        const feature = dataStore.selectedFeature;
        console.log('RightView - Computing selectedFeature:', {
          feature: feature,
          properties: feature?.properties,
        });
        return feature;
      });

      //  
      return {
        getActiveAnalysisCount, // 
        selectedFeature: selectedFeatureComputed, // 
      };
    },
  };
</script>

<template>
  <div class="my-right-panel h-100 d-flex flex-column overflow-hidden">
    <!--   -->
    <div class="flex-grow-1 overflow-auto">
      <!--   -->
      <div v-show="activeRightTab === 'properties'" class="h-100">
        <PropertiesTab
          :selected-feature="selectedFeature"
          @highlight-feature="$emit('highlight-feature', $event)"
        />
      </div>
    </div>
  </div>
</template>

<style scoped></style>
