// =================================================================================
//   (Helper Functions)
// =================================================================================

/**
 * 
 *
 * @param {string} iconKey - 
 * @param {string} lang -  ('zh' | 'en')
 * @returns {object} 
 */
export function getIcon(iconKey, lang = 'zh') {
  const iconInfo = ICONS[iconKey];
  if (!iconInfo) {
    console.warn(`: ${iconKey}`);
    return {
      text: iconKey,
      icon: 'fas fa-question-circle',
    };
  }

  return {
    text: iconInfo[lang] || iconInfo.zh,
    icon: iconInfo.icon,
  };
}
